<template>
    <div v-if="supplier.id">
        <div class="supplier-info__item supplier-info__item--about" v-if="supplier.aboutTextClear">
            <div class="supplier-views">
                <svg>
                    <use xlink:href="@/assets/img/mob-sprite.svg#eye"></use>
                </svg>
                <span class="text-color-white text-size-xs ml4">{{ supplier.views_counter }}</span>
            </div>
            <a class="supplier-link" target="_blank" :href="'http://' + supplier.site">
                <span class="text-color-white text-size-s mr4">Сайт</span>
                <svg>
                    <use xlink:href="@/assets/img/mob-sprite.svg#link"></use>
                </svg>
            </a>
            <div class="top-icon">
                <img src="@/assets/img/supplier/supplier-about.svg" alt="">
            </div>

            <div class="ttl text-center text-size-l text-bold text-color">
                О компании
            </div>
            <div class="text text-size-m" v-html="supplier.aboutTextClear">

            </div>

            <gallery :items="aboutMedia"></gallery>
            <div class="separator">
                <svg>
                    <use xlink:href="@/assets/img/mob-sprite.svg#supplier-separator"></use>
                </svg>
            </div>
        </div>

        <div class="supplier-info__item supplier-info__item--advantage" v-if="advantages.length">
            <div class="top-icon">
                <img src="@/assets/img/supplier/supplier-advantage.svg" alt="">
            </div>

            <div class="ttl text-center text-size-l text-bold text-color">
                Наши преимущества
            </div>
            <div v-for="(item, index) in advantages">
                <div class="text text-size-m" v-html="item"></div>
                <div class="separator">
                    <svg>
                        <use xlink:href="@/assets/img/mob-sprite.svg#supplier-separator"></use>
                    </svg>
                </div>
            </div>
        </div>

        <div class="supplier-info__item supplier-info__item--portfolio" v-if="portfolio.length">
            <div class="top-icon">
                <img src="@/assets/img/supplier/supplier-portfolio.svg" alt="">
            </div>

            <div class="ttl text-center text-size-l text-bold text-color">
                Портфолио
            </div>

            <gallery :items="portfolio"></gallery>
            <div class="separator">
                <svg>
                    <use xlink:href="@/assets/img/mob-sprite.svg#supplier-separator"></use>
                </svg>
            </div>
        </div>

        <div class="supplier-info__item supplier-info__item--instruments" v-if="supplier.about.equipment">
            <div class="top-icon">
                <img src="@/assets/img/supplier/supplier-instruments.svg" alt="">
            </div>

            <div class="ttl text-center text-size-l text-bold text-color">
                Инструменты и оборудование
            </div>
            <div class="text text-size-m" v-html="supplier.about.equipment"></div>
            <gallery :items="equipmentMedia"></gallery>
            <div class="separator">
                <svg>
                    <use xlink:href="@/assets/img/mob-sprite.svg#supplier-separator"></use>
                </svg>
            </div>
        </div>

        <div class="supplier-info__item supplier-info__item--shops" v-if="shops.length">
            <div class="top-icon">
                <img src="@/assets/img/supplier/supplier-shops.svg" alt="">
            </div>

            <div class="ttl text-center text-size-l text-bold text-color">
                Магазины и шоурумы
            </div>
            <div v-if="typeof(shops) === 'string'">
                <div class="shop-item mt24">
                    <div class="text text-size-m text-center" v-html="shops"></div>
                </div>
                <gallery v-if="shopsPhotos.length && shopsPhotos[0].length" :items="shopsPhotos[0]"></gallery>
                <div class="separator mt24">
                    <svg>
                        <use xlink:href="@/assets/img/mob-sprite.svg#supplier-separator"></use>
                    </svg>
                </div>
            </div>
            <div v-else v-for="(shop, shopIndex) in shops" :key="shop.id">
                <div class="shop-item mt24">
                    <div class="shop-addr text-color-primary text-bold text-center text-size-m">
                        {{shop.address}}
                    </div>
                    <div class="text-color-grey text-size-s text-center mt4">
                        {{shop.schedule}}
                    </div>
                    <div class="text text-size-m text-center" v-html="shop.desc">
                    </div>
                </div>
                <gallery v-if="shopsPhotos[shopIndex] && shopsPhotos[shopIndex].length" :items="shopsPhotos[shopIndex]"></gallery>
                <div class="separator mt24">
                    <svg>
                        <use xlink:href="@/assets/img/mob-sprite.svg#supplier-separator"></use>
                    </svg>
                </div>
            </div>

            <img v-if="showMap" src="@/assets/img/map.jpg" alt="" class="mt24">
        </div>

        <div class="supplier-info__item supplier-info__item--offices" v-if="offices.length">
            <div class="top-icon">
                <img src="@/assets/img/supplier/supplier-offices.svg" alt="">
            </div>

            <div class="ttl text-center text-size-l text-bold text-color">
                Наши офисы
            </div>
            <div v-if="typeof(offices) === 'string'">
                <div class="shop-item mt24">
                    <div class="text text-size-m text-center" v-html="offices"></div>
                </div>
                <gallery :items="officePhotos[0]"></gallery>
                <div class="separator mt24">
                    <svg>
                        <use xlink:href="@/assets/img/mob-sprite.svg#supplier-separator"></use>
                    </svg>
                </div>
            </div>
            <div v-else v-for="office in offices">
                <div class="shop-item mt24">
                    <div class="shop-addr text-color-primary text-bold text-center text-size-m">
                        {{office.address}}
                    </div>
                    <div class="text-color-grey text-size-s text-center mt4">
                        {{office.schedule}}
                    </div>
                    <div class="text text-size-m text-center" v-html="office.desc">
                    </div>
                </div>
                <gallery :items="officePhotos[0]"></gallery>
                <div class="separator mt24">
                    <svg>
                        <use xlink:href="@/assets/img/mob-sprite.svg#supplier-separator"></use>
                    </svg>
                </div>
            </div>

            <img v-if="showMap" src="@/assets/img/map.jpg" alt="" class="mt24">
        </div>

        <div class="supplier-info__item supplier-info__item--production" v-if="supplier.about.production">
            <div class="top-icon">
                <img src="@/assets/img/supplier/supplier-production.svg" alt="">
            </div>

            <div class="ttl text-center text-size-l text-bold text-color">
                Производство
            </div>
            <div class="text text-size-m" v-html="supplier.about.production"></div>

            <gallery :items="productionMedia"></gallery>
            <div class="separator">
                <svg>
                    <use xlink:href="@/assets/img/mob-sprite.svg#supplier-separator"></use>
                </svg>
            </div>
        </div>

        <div class="supplier-info__item supplier-info__item--staff" v-if="supplier.about.collective">
            <div class="top-icon">
                <img src="@/assets/img/supplier/supplier-staff.svg" alt="">
            </div>

            <div class="ttl text-center text-size-l text-bold text-color">
                Наш коллектив
            </div>
            <div class="text text-size-m" v-html="supplier.about.collective"></div>

            <gallery :items="teamMedia"></gallery>
            <div class="separator">
                <svg>
                    <use xlink:href="@/assets/img/mob-sprite.svg#supplier-separator"></use>
                </svg>
            </div>
        </div>

        <div class="supplier-info__item supplier-info__item--staff" v-if="supplier.about.other">
            <div class="top-icon">
                <img src="@/assets/img/supplier/supplier-staff.svg" alt="">
            </div>
            <div class="ttl text-center text-size-l text-bold text-color" v-html="supplier.about.other"></div>

            <div class="text text-size-m" v-html="supplier.about.otherTitle"></div>

            <gallery :items="otherMedia"></gallery>
            <div class="separator">
                <svg>
                    <use xlink:href="@/assets/img/mob-sprite.svg#supplier-separator"></use>
                </svg>
            </div>
        </div>

        <div class="separator-bottom">
            <svg>
                <use xlink:href="@/assets/img/mob-sprite.svg#supplier-separator2"></use>
            </svg>
        </div>
        <div class="text-size-m text-center text-color-white mt32">
            <div v-html="supplier.about.resume"></div>
            <br>
            <span class="text-bold" v-if="supplier.about.sign_position">
                С уважением,<br>
                {{ supplier.about.sign_position }} «{{ supplier.name }}»<br>
                {{ supplier.about.sign_second_name }} {{ supplier.about.sign_name }} {{ supplier.about.sign_middle_name }}
            </span>
            <span v-else class="text-bold" v-html="supplier.about.sign">
                {{ supplier.about.sign }}
            </span>
        </div>
        <div class="separator mt32">
            <svg>
                <use xlink:href="@/assets/img/mob-sprite.svg#supplier-separator"></use>
            </svg>
        </div>
        <div class="text-size-m text-center text-color-white mt32">
            {{ supplier.ownership }}
            <div v-if="supplier.contract.inn">ИНН {{ supplier.contract.inn }}</div>
            <div v-if="supplier.contract.ogrnip">ОГРНИП {{ supplier.contract.ogrnip }}</div>
            <div v-if="supplier.contract.ogrn">ОГРН {{ supplier.contract.ogrn }}</div>
        </div>

        <footer class="block-footer mt32">
            <div class="footer-inner">
                <div class="f-logo"><router-link to="/">
                    <svg class="icon-f-logo">
                        <use xlink:href="@/assets/img/mob-sprite.svg#f-logo"></use>
                    </svg></router-link></div>
                <div class="f-feedback f-txt"><span>Если вам предоставили скидку<br>без купона – </span><a class="hf" href="javascript:void(0)" @click="showPopup('mailZems')">сообщите нам</a></div>
                <div class="f-copyright f-txt">© 1980-2092 Алексей Земсков</div>
                <div class="f-contact f-txt"><router-link class="hf" to="/contacts/">Контактная информация</router-link></div>
                <div class="f-public-offer f-txt"><router-link class="hf" to="/offer/">Публичная оферта</router-link></div>
            </div>
        </footer>
    </div>
    <loader v-else></loader>

</template>



<script>
    import {mapState} from 'vuex';
    import Tooltip from '@/components/Tooltip.vue';
    import PartnerAboutMainText from '@/components/partner/PartnerAboutMainText.vue';
    import Gallery from '@/components/Gallery.vue';
    import Loader from '@/components/Loader.vue';

    export default {
        name: "PartnerAboutTextTab",
        components: {
            Tooltip,
            Gallery,
            Loader
        },
        props: {
            partner: {}
        },
        data: function() {
            return {
                showMap: false,
            }
        },
        mounted: function () {
            this.init();
        },
        beforeDestroy() {
            this.$store.commit('partner/setSupplier', []);
        },
        computed: {
            ...mapState({
                user: state => state.users.user,
                popups: state => state.popups.items,
                supplier: state => state.partner.itemDetail,
                action: state => state.actions.detail,
            }),
            aboutMedia() {
                let aboutTextMedia = [];
                let data = this.supplier.about.aboutTextPhotos;
                let dataVideo = this.supplier.about.aboutTextVideos;
                for (let index in data) {
                    aboutTextMedia.push({
                        id: data[index].id,
                        icon: data[index].icon,
                        path: data[index].path,
                        width: data[index].width,
                        height: data[index].height,
                        mediaType: 'photo',
                        type: 'photo'
                    })
                }

                for (let index in dataVideo) {
                    if (!dataVideo[index]) {
                        continue;
                    }

                    aboutTextMedia.push({
                        id: dataVideo[index].id,
                        icon: dataVideo[index].preview,
                        preview: dataVideo[index].preview,
                        path: dataVideo[index].url,
                        embedId: dataVideo[index].embedId,
                        mediaType: 'video',
                        type: 'video'
                    })
                }

                return aboutTextMedia;
            },

            equipmentMedia() {
                let equipmentMedia = [];
                let data = this.supplier.about.equipmentPhotos;
                let dataVideo = this.supplier.about.equipmentVideos;
                for (let index in data) {
                    equipmentMedia.push({
                        id: data[index].id,
                        icon: data[index].icon,
                        path: data[index].path,
                        width: data[index].width,
                        height: data[index].height,
                        mediaType: 'photo',
                        type: 'photo'
                    })
                }

                for (let index in dataVideo) {
                    if (!dataVideo[index]) {
                        continue;
                    }

                    equipmentMedia.push({
                        id: dataVideo[index].id,
                        icon: dataVideo[index].preview,
                        preview: dataVideo[index].preview,
                        path: dataVideo[index].url,
                        embedId: dataVideo[index].embedId,
                        mediaType: 'video',
                        type: 'video'
                    })
                }

                return equipmentMedia;
            },
            productionMedia() {
                let productionMedia = [];
                let data = this.supplier.about.productionPhotos;
                let dataVideo = this.supplier.about.productionVideos;

                for (let index in data) {
                    if (!data[index]) {
                        continue;
                    }
                    productionMedia.push({
                        id: data[index].id,
                        icon: data[index].icon,
                        path: data[index].path,
                        width: data[index].width,
                        height: data[index].height,
                        mediaType: 'photo',
                        type: 'photo'
                    })
                }

                for (let index in dataVideo) {
                    if (!dataVideo[index]) {
                        continue;
                    }
                    productionMedia.push({
                        id: dataVideo[index].id,
                        icon: dataVideo[index].preview,
                        preview: dataVideo[index].preview,
                        path: dataVideo[index].url,
                        embedId: dataVideo[index].embedId,
                        mediaType: 'video',
                        type: 'video'
                    })
                }

                return productionMedia;
            },
            otherMedia() {
                let otherMedia = [];
                let data = this.supplier.about.otherPhotos;
                let dataVideo = this.supplier.about.otherVideos;

                for (let index in data) {
                    if (!data[index]) {
                        continue;
                    }
                    otherMedia.push({
                        id: data[index].id,
                        icon: data[index].icon,
                        path: data[index].path,
                        width: data[index].width,
                        height: data[index].height,
                        mediaType: 'photo',
                        type: 'photo'
                    })
                }

                for (let index in dataVideo) {
                    if (!dataVideo[index]) {
                        continue;
                    }
                    otherMedia.push({
                        id: dataVideo[index].id,
                        icon: dataVideo[index].preview,
                        preview: dataVideo[index].preview,
                        path: dataVideo[index].url,
                        embedId: dataVideo[index].embedId,
                        mediaType: 'video',
                        type: 'video'
                    })
                }

                return otherMedia;
            },
            teamMedia() {
                let teamMedia = [];
                let data = this.supplier.about.collectivePhotos;
                let dataVideo = this.supplier.about.collectiveVideos;

                for (let index in data) {
                    if (!data[index]) {
                        continue;
                    }
                    teamMedia.push({
                        id: data[index].id,
                        icon: data[index].icon,
                        path: data[index].path,
                        width: data[index].width,
                        height: data[index].height,
                        mediaType: 'photo',
                        type: 'photo'
                    })
                }

                for (let index in dataVideo) {
                    if (!dataVideo[index]) {
                        continue;
                    }
                    teamMedia.push({
                        id: dataVideo[index].id,
                        icon: dataVideo[index].preview,
                        preview: dataVideo[index].preview,
                        path: dataVideo[index].url,
                        embedId: dataVideo[index].embedId,
                        mediaType: 'video',
                        type: 'video'
                    })
                }

                return teamMedia;
            },
            advantages() {
                let list = this.supplier.about.advantages_list;
                if (!list) {
                    list = [];
                    if (this.supplier.about.advantages) {
                        list = [this.supplier.about.advantages];
                    }
                }
                return list;
            },
            shops() {
                let shops = [];

                if (typeof (this.supplier.about.shops) === 'string') {
                    shops = this.supplier.about.shops;
                } else if (this.supplier.about.shops.length) {
                    shops = this.supplier.about.shops;
                }

                return shops;
            },
            offices() {
                let offices = [];

                if (this.supplier.about.offices) {
                    if (typeof (this.supplier.about.offices) === 'string') {
                        offices = this.supplier.about.offices;
                    } else if (this.supplier.about.offices.length) {
                        offices = this.supplier.about.offices;
                    }
                }

                return offices;
            },
            shopsPhotos() {
                let shopsPhotos = [];
                let data = this.supplier.about.shopsPhotos;

                if (typeof (this.shops) === 'string') {
                    for (let index in data) {
                        shopsPhotos.push({
                            id: data[index].id,
                            icon: data[index].icon,
                            path: data[index].path,
                            width: data[index].width,
                            height: data[index].height,
                            mediaType: 'photo',
                        });
                    }
                } else {
                    for (let index in data) {
                        shopsPhotos.push([]);
                        for (let photoIndex in data[index]) {
                            shopsPhotos[index].push({
                                id: data[index][photoIndex].id,
                                icon: data[index][photoIndex].icon,
                                path: data[index][photoIndex].path,
                                width: data[index][photoIndex].width,
                                height: data[index][photoIndex].height,
                                mediaType: 'photo',
                            });
                        }
                    }
                }


                return shopsPhotos;
            },
            officePhotos() {

                let officePhotos = [];
                let data = this.supplier.about.officePhotos;

                for (let index in data) {
                    officePhotos.push([]);
                    for (let officeIndex in data[index]) {
                        officePhotos[index].push({
                            id: data[index][officeIndex].id,
                            icon: data[index][officeIndex].icon,
                            path: data[index][officeIndex].path,
                            width: data[index][officeIndex].width,
                            height: data[index][officeIndex].height,
                            mediaType: 'photo',
                        });
                    }
                }

                return officePhotos;
            },
            portfolio() {

                let portfolio = [],
                    data = this.supplier.about.portfolioPhotos;

                for (let index in data) {
                    portfolio.push({
                        id: data[index].id,
                        icon: data[index].icon,
                        path: data[index].path,
                        width: data[index].width,
                        height: data[index].height,
                        mediaType: 'photo',
                    })
                }

                return portfolio;
            }
        },
        methods: {
            showPopup(popup) {
                this.$store.commit('popups/open',{name:popup});
            },
            init() {
                this.$store.dispatch('partner/initDetail', {id: this.action.provider.id}).then(() => {

                });
            }
        },
    }

</script>
