<template>
    <div>
        <catalog-list-search></catalog-list-search>
    </div>
</template>

<script>
    import CatalogSections from '@/components/catalog/CatalogSections.vue'
    import CatalogListSearch from '@/components/catalog/CatalogListSearch.vue'
    import { mapState } from 'vuex'

    export default {
        props: { },
        name: "CatalogSearch",
        components: {
            CatalogSections,
            CatalogListSearch,
        },
        created () {

        },
        mounted () {
            this.init()
        },
        computed: {
            totalCoupons () {
                return this.$store.getters['actions/getTotalCoupons'];
            },
            ...mapState({
                currentCity: state => state.cities.current,
                viewType: state => state.actions.viewType,
                popups: state => state.popups.items,
                sort: state => state.actions.sort,
                searchQuery: state => state.search.query,
            }),
        },
        methods:{
            init() {
                this.$store.dispatch('actions/getCategoriesList', {})
            },
        }
    }
</script>
