<template>
	<div>
		<catalog-list :favorites="favorites" v-if="viewType === 'list'"></catalog-list>
		
		<new-catalog-detail v-else-if="viewType === 'detail' && curTest == 1"></new-catalog-detail>
		<catalog-detail v-else-if="viewType === 'detail' && curTest == 2"></catalog-detail>
	</div>
</template>

<script>
    import CatalogSections from '@/components/catalog/CatalogSections.vue'
    import CatalogList from '@/components/catalog/CatalogList.vue'
    import CatalogDetail from '@/components/catalog/CatalogDetail.vue'
		import NewCatalogDetail from '@/components/catalog/NewCatalogDetail.vue'
    import { mapState } from 'vuex'

    export default {
		props: {
            favorites: {
		        default: function () {
					return false
                }
			}
		},
		name: "Catalog",
		components: {
			CatalogSections,
            CatalogList,
            CatalogDetail,
						NewCatalogDetail
		},
        created () {
            this.init()
		},
		mounted () {

		},
        computed: {
            totalCoupons () {
                return this.$store.getters['actions/getTotalCoupons'];
            },
            viewType () {
                if(this.$route.params.sid > 0){
					return 'list';
				} else if(this.$route.params.id > 0) {
                    return 'detail';
				} else {
                    return 'list';
				}
            },
			...mapState({
								currentCity: state => state.cities.current,
                popups: state => state.popups.items,
                sort: state => state.actions.sort,
                sectionFavorites: state => state.actions.favorites.section,
								curTest: state => state.abtest.currentTest,
			}),
        },

		methods:{
            openCitySelect() {
                this.$store.commit('popups/open', {name: this.popups.citySelect.name});
            },
			init() {
                this.$store.dispatch('actions/initialCatalogState', {route: this.$route});
            }
		}
    }
</script>
