<template>
  <div class="content">
    <catalog :favorites="true"></catalog>
  </div>
</template>

<script>
    import Catalog from '@/components/catalog/Catalog.vue'
    import CatalogMap from '@/components/catalog/CatalogMap.vue'
    import { mapState } from 'vuex'

    export default {
        metaInfo() {
            return {
                title: 'Избранное',
                titleTemplate: this.$store.state.options.titleTemplate
            }
        },
        name: "Favorites",
        components: {
            Catalog,
            CatalogMap
        },
        computed: {
            isEmptyLoad () {
                return !this.loadAction && !(this.coupons.length > 0);
            },
            currentSection () {
                return this.$route.params.sid;
            },
            markers () {
                let markers = [];
                for(let coupon of this.coupons) {
                    if(coupon.coords.length > 0) {
                        for (let coordIndex in coupon.coords) {
                            let coord = coupon.coords[coordIndex];
                            markers.push(
                                {
                                    id: coupon.id+'-'+coordIndex,
                                    type: 'placemark', // define properties here
                                    coords: coord.split(','), // define options here
                                    coupon: {
                                        image: '//api.zemskidki.ru/'+coupon.image,
                                        name:  coupon.name,
                                        id: coupon.id
                                    }
                                }
                            );
                        }
                    }

                }
                return markers;
            },
            ...mapState({
                loadAction: state => state.actions.isLoad,
                currentCity:  state => state.cities.current,
                hidePromo:  state => state.actions.hidePromo,

                favoritesItems: state => state.actions.favorites.items,
                visitedItems: state => state.actions.visited.items,
            }),
            coupons(){
                return  this.$store.getters['actions/getFavorites']();
            }
        },
        methods: {
            toggleMap() {
                this.$store.commit('actions/toggleMap');
            },
        },        
        mounted: function () {
            ym(39511350, 'hit', this.$route.path, {title: 'Избранное | ZemSkidki.ru'});
        }
    }
</script>