<template>
    <div class="popup-info">
        <div>
        </div>
        
        <div class="text mt32">
            <div class="text-size-xxl text-color-red text-bolder">Вы не можете написать отзыв на купон,<br>которым не пользовались</div>
            
            <p class="mt24">
                У нас допускаются только честные отзывы, которые может оставить<br>
                только владелец купона и только после его использования.
            </p>
        </div>
        
        <div class="mt32">
            <span class="btn btn--white-red" @click="close">Справедливо!</span>
        </div>
        
        <div class="mt32">
            P.S. При этом вы всегда можете <a @click="openCouponClaim" href="javascript:void(0)">пожаловаться на купон</a> руководителю проекта.
        </div>
    </div>
</template>

<script>
    import Popup from '@/components/modal/Popup.vue'
    import {mapState} from 'vuex'
    
    export default {
        name: "OpinionError",
        methods: {
            close() {
                this.$store.commit('popups/close', {name: this.popups.opinionAdd.name});
            },
            openCouponClaim: function () {
                this.$store.commit('popups/close', {name: this.popups.opinionAdd.name});
                this.$store.commit('popups/open', {name: this.popups.couponClaim.name});
            },
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        }
    }
</script>

