<template>
    <div>
        <div class="coupon-list-block coupon-list-main">
            <div class="coupon-list-inner inner">
                <div class="list-name" v-if="!isEmptyLoad">
                    <span class="txt">Результаты поиска</span>
                    <div class="sort" v-if="true/*!favorites*/" @click="showSort()">
                        <svg class="icon">
                            <use xlink:href="@/assets/img/mob-sprite.svg#sort"></use>
                        </svg>
                    </div>
                </div>
                <div class="coupon-list" v-if="!isEmptyLoad">
                    <catalog-list-item v-for="coupon in coupons" :item="coupon" :key="coupon.id"></catalog-list-item>

                </div>
                <loader v-if="loadAction" class=""></loader>
            </div>
        </div>
        <div  v-if="isEmptyLoad && !loadAction"  class="no-favourites no-favourites--search">
            <div class="icons">
                <svg class="icon-fav fav">
                    <use xlink:href="@/assets/img/mob-sprite.svg#emoji-search2"></use>
                </svg>
            </div>
            <div class="text-size-xl text-bold mt16 text-color-white">
                Результаты поиска
            </div>
            <div class="nofav">По данному запросу<br>
                в вашем городе <a href="javascript:void(0);" class="city" @click="openCitySelect">{{currentCity.name}}</a><br>
                ничего не найдено:(</div>
            <div class="addthem">Попробуйте изменить запрос<br>или поищите скидки в общем списке</div>
            <div class="button-block icon-big">
                <button class="btn" @click="goHome()">
                    <svg class="icon long-ticket">
                        <use xlink:href="@/assets/img/mob-sprite.svg#long-ticket"></use>
                    </svg>
                    <div class="txt">Смотреть все скидки</div>
                </button>
            </div>
        </div>
        <div @click="hideSort" :class="{'fadeIn':isShowSort}" class="search-inpt-bg"></div>
        <catalog-sort></catalog-sort>
    </div>

    <!-- end coupon-list -->
</template>

<script>
    import { mapState } from 'vuex'
    import CatalogListItem from '@/components/catalog/CatalogListItem.vue'
    import CatalogSort from '@/components/catalog/CatalogSort.vue'
    import Loader from '@/components/Loader.vue';

    export default {
        name: "CatalogListSearch",
        components: {
            CatalogListItem,
            CatalogSort,
            Loader,
        },
        created () {

        },

        mounted() {
            window.addEventListener('scroll', this.handleScroll);
        },
        beforeDestroy() {
            window.removeEventListener('scroll', this.handleScroll);
        },
        computed: {
            title () {
                return this.$store.state.search.query;
            },
            isEmptyLoad () {
                return !this.loadAction && !(this.coupons.length > 0);
            },
            currentSection () {
                return this.$route.params.sid;
            },
            markers () {
                let markers = [];
                for(let coupon of this.coupons) {
                    if(coupon.coords.length > 0) {
                        for (let coordIndex in coupon.coords) {
                            let coord = coupon.coords[coordIndex];
                            markers.push(
                                {
                                    id: coupon.id+'-'+coordIndex,
                                    type: 'placemark', // define properties here
                                    coords: coord.split(','), // define options here
                                    coupon: {
                                        image: '//api.zemskidki.ru/'+coupon.image,
                                        name:  coupon.name,
                                        id: coupon.id
                                    }
                                }
                            );
                        }
                    }

                }
                return markers;
            },
            ...mapState(
                {
                    loadAction: state => state.actions.isLoad,
                    currentCity:  state => state.cities.current,
                    coupons:  state => state.actions.coupons,
                    cityCurrentName:  state => state.cities.current.name.toUpperCase(),
                    isShowSort:  state => state.actions.sort.show,
                    sort: state => state.actions.sort,
                    popups: state => state.popups.items,
                }
            ),
            currentSortName(){
                return this.sort.order[this.sort.current.order].name
            }
        },
        data(){
            return {
                coords: []
            }
        },
        methods: {
            goHome: function(){
              this.$router.push('/');
              this.$store.commit('actions/scrollToCatalogList', true);
            },
            handleScroll: function () {
                let scroll = window.scrollY;
                let viewport = window.innerHeight;
                let bodyHeight = document.getElementById('app').offsetHeight;
                if(bodyHeight-80 <= scroll + viewport){
                    this.$store.dispatch('actions/getCouponList', {currentSection: this.currentSection, isSearch: true})
                }
            },
            openCitySelect() {
                this.$store.commit('popups/open', {name: this.popups.citySelect.name});
            },
            toggleSearch () {
                this.$store.commit('search/toggleSearchTop');
            },
            showSort(){
                this.$store.commit('actions/setShowSort',true);
                document.getElementsByTagName('body')[0].classList.add('nonscroll');
            },
            hideSort(){
                this.$store.commit('actions/setShowSort',false);
                document.getElementsByTagName('body')[0].classList.remove('nonscroll');
            },
        }

    }
</script>
