<template>
    <div class="gallery-wrap">
        <!--
                - у миниатюр с видео есть класс item--video и блок item__play
                - при клике открывается модалка с видео с параметрами, как на главной
            -->
       <vue-picture-swipe :class="{'two-items': countItems == 3, 'one-item': countItems == 2, 'three-items': countItems == 4, 'four-items': countItems == 5}" v-if="type == 'photo-coupon' || type == 'review-photos'" ref="pictureSwipeCoupon" :items="viewerImages" :options="{maxSpreadZoom: 4, loop: true,shareEl: false,fullscreenEl: false, arrowEl: false,zoomEl: false,indexIndicatorSep: ' из ',preloaderEl: false}">
       </vue-picture-swipe>
       <div class="videos-mini" v-if="type == 'photo-coupon' && viewerVideos.length > 0">
           <div class="videos-mini__item" v-for="(video,index) in viewerVideos" @click="showVideo(video);" :key="'video_'+index">
               <div class="play-icon">
                   <svg>
                       <use xlink:href="@/assets/img/mob-sprite.svg#play"></use>
                   </svg>
               </div>
               <a :style="{'background-image': 'url('+ video.preview +')'}"></a>
           </div>
       </div>
        <vue-picture-swipe v-else :id="uid" :class="{'hide-gallery': true}" ref="pictureSwipePhoto" :items="viewerImagesSupplier" :options="{loop: true,shareEl: false,fullscreenEl: false, arrowEl: false,zoomEl: false,indexIndicatorSep: ' из ',preloaderEl: false, maxSpreadZoom: 4,
        getDoubleTapZoom: function(isMouseClick, item) {if(isMouseClick) {return 1;} else {return item.initialZoomLevel < 0.7 ? 1 : 1.5;}},}">
       </vue-picture-swipe>
        <popup :width="320" @close="close" :show="modal" :add-class="'popup--video'" :is-set-blank="true">
          <div class="viewer-gallery">
            <div class="close" @click="close()"><span class="i icon-close">
              <svg class="icon">
                <use xlink:href="@/assets/img/mob-sprite.svg#close"></use>
              </svg></span>
            </div>
            <iframe width="320"
                      height="150"
                      :src="'https://www.youtube.com/embed/'+currentId"
                      frameborder="0"
                      allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
                      allowfullscreen></iframe>
          </div>
        </popup>


        <div v-if="countItems > 1 && type != 'photo-coupon' && type != 'review-photos'" class="action-detail__carousel">

            <hooper  ref="viewer-carousel" class="carousel-thumbs hooper" :class="{'carousel-thumbs--video':type === 'video'}" :wheelControl="false" :infiniteScroll="false" :itemsToShow="2">
                <slide :key="index"
                       v-for="(item,index) in items">
                    <div v-if="item.mediaType === 'photo'"
                         class="item">
                        <a @click="showViewer(index)"
                           :style="{'background-image': 'url(//api.zemskidki.ru'+item.icon+')'}">
                          <div class="count">
                            {{index + 1}} из {{countItems}}
                          </div>
                         </a>
                    </div>


                    <div v-else
                         class="item item--video">
                        <a @click="showVideo(item)" href="javascript:void(0)" :style="{'background-image': 'url('+item.preview+')'}">
                            <div class="play-icon">
                                <svg>
                                    <use xlink:href="@/assets/img/mob-sprite.svg#play"></use>
                                </svg>
                            </div>
                            <div class="count">
                              {{index + 1}} из {{countItems}}
                            </div>
                        </a>
                    </div>
                </slide>
            </hooper>
        </div>
        <div v-if="countItems == 1 && type != 'photo-coupon'" class="action-detail__carousel">
            <div class="" v-for="(item,index) in items">
                <div v-if="item.mediaType === 'photo'" class="solo-photo">
                    <a @click="showViewer(index)"
                       :style="{'background-image': 'url(//api.zemskidki.ru'+item.icon+')'}"></a>
                </div>


                <div v-else class="solo-video item--video">
                    <a @click="showVideo(item)" href="javascript:void(0)" :style="{'background-image': 'url('+item.preview+')'}">
                        <div class="play-icon">
                            <svg>
                                <use xlink:href="@/assets/img/mob-sprite.svg#play"></use>
                            </svg>
                        </div>
                    </a>
                </div>
            </div>
        </div>

        <!--
        <viewer v-if="type === 'photo' && viewerShow" :options="viewerOptions" :images="viewerImages"
                @inited="inited"
                class="viewer mt24" ref="viewer"
        >
            <img style="display: none;" v-for="(item,index) in items" :src="'//api.zemskidki.ru'+item.path" :key="index" width="200" height="140">
        </viewer>
      -->
    </div>
</template>

<script>
    import {mapState} from 'vuex';
    import Popup from '@/components/modal/Popup.vue';
    import { Hooper, Slide, Navigation as HooperNavigation } from 'hooper'; // carousel
    import Viewer from "@/components/viewer/v-viewer/src/component.vue" // popup slider
    import VuePictureSwipe from 'vue-picture-swipe';


    export default {
        name: "NewGallery",
        components: {
            Hooper,
            Slide,
            HooperNavigation,
            Popup,
            Viewer,
            VuePictureSwipe,
        },
        props: {
            type: {
                default: 'photo', // video
            },
            items: {
                default(){
                    return [];
                }
            },
            main: {
                default(){
                    return {};
                }
            },
            firstphoto: {
              default(){
                return {};
              }
            }
        },
        data(){
            return {

                currentId: false,
                modal: false,
                viewerShow: false,
                currentSlideViewer: 0,
                viewerOptions: {
                    inline: false,
                    button: true,
                    navbar: false,
                    title: false,
                    toolbar: true,
                    tooltip: false,
                    movable: false,
                    zoomable: false,
                    rotatable: false,
                    scalable: false,
                    transition: false,
                    fullscreen: true,
                    keyboard: true,
                    loop: true,
                },
                unsubscribe: '',
                uid: null,
            }
        },
        beforeMount(){
            this.$nextTick(function () {
                this.viewerShow = false;
            });
        },
        mounted: function () {
            this.uid = 'pswpid' + this._uid;
            this.unsubscribe = this.$store.subscribe((mutation, state) => {
                if(mutation.type=='setTabClick'){
                        this.updateCarousel();
                }
            });
            if(this.type == 'photo-coupon'){
                let myGallery = document.querySelector('.gallery-wrap').querySelector('.my-gallery');
                let figures = document.querySelector('.gallery-wrap').querySelectorAll('figure');
                figures.forEach(function(el, index) {
                    if(index == 0){
                      el.classList.add('hide-first');
                    }
                 });
                 if(this.viewerImages.length == 5 && this.viewerVideos.length < 1){
                    figures.forEach(function(el, index) {
                          if(index == 0){
                            el.classList.add('hide-first');
                          }
                         el.classList.add('three');
                      });

                  }else if(this.viewerImages.length > 5 && this.viewerVideos.length < 1){
                    let self = this;
                    figures.forEach(function(el, index) {
                          if(index == 0){
                            el.classList.add('hide-first');
                          }
                          if(index == 4){
                            let span = document.createElement('span');
                            span.innerHTML = 'ещё ' + (self.viewerImages.length - 4);
                            el.querySelector('a').prepend(span);
                          }
                          el.classList.add('more');
                      });
                  }
                  if(this.viewerVideos.length == 1){
                      let self = this;
                      myGallery.style.paddingLeft = '76px';
                      figures.forEach(function(el, index) {
                            if(index == 0 || index > 3){
                              el.classList.add('hide');
                            }
                            if(index == 3 && self.viewerImages.length > 4){
                              let span = document.createElement('span');
                              span.innerHTML = 'ещё ' + (self.viewerImages.length - (4 - self.viewerVideos.length));
                              el.querySelector('a').prepend(span);
                            }
                               el.classList.add('more');
                       });
                  }else if(this.viewerVideos.length == 2){
                      let self = this;
                      myGallery.style.paddingLeft = '152px';
                      figures.forEach(function(el, index) {
                            if(index == 0 || index > 2){
                              el.classList.add('hide');
                            }
                            if(index == 2 && self.viewerImages.length > 3){
                              let span = document.createElement('span');
                              span.innerHTML = 'ещё ' + (self.viewerImages.length - (4 - self.viewerVideos.length));
                              el.querySelector('a').prepend(span);
                            }
                             el.classList.add('more');
                      });
                  }else if(this.viewerVideos.length == 3){
                      let self = this;
                      myGallery.style.paddingLeft = '228px';
                      figures.forEach(function(el, index) {
                            if(index == 0 || index > 1){
                              el.classList.add('hide');
                            }
                            el.classList.add('more');
                            if(index == 1 && self.viewerImages.length > 2 ){
                              let span = document.createElement('span');
                              span.innerHTML = 'ещё ' + (self.viewerImages.length - (4 - self.viewerVideos.length));
                              el.querySelector('a').prepend(span);
                            }
                        });
                  }
            }
        },
        beforeDestroy: function(){
            this.unsubscribe();
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
            countItems() {
                return this.items.length;
            },
            colItems() {
                let col = 6;
                switch (this.countItems) {
                    case 1:
                        col = 24;
                        break;
                    case 2:
                        col = 12;
                        break;
                    case 3:
                        col = 8;
                        break;
                }
                return col;
            },
            viewerImages(){
                let images = [];
                for(let item in this.items){
                  if(this.items[item].mediaType == 'photo'){
                    images.push({src: '//api.zemskidki.ru'+this.items[item].path, thumbnail: '//api.zemskidki.ru'+this.items[item].path, w: this.items[item].width, h: this.items[item].height });
                    }
                }
                return images;
            },
            viewerVideos(){
                let videos = [];
                for(let item in this.items){
                  if(this.items[item].mediaType == 'video'){
                    videos.push({src: this.items[item].url, preview: this.items[item].preview, icon: this.items[item].preview, id: this.items[item].id});
                  }
                }
                return videos;
            },
            viewerImagesSupplier(){
                let images = [];
                for(let item in this.items){
                    if (this.items[item].mediaType === 'photo') {
                        images.push({
                            src: '//api.zemskidki.ru' + this.items[item].path,
                            thumbnail: '//api.zemskidki.ru' + this.items[item].path,
                            w: this.items[item].width,
                            h: this.items[item].height
                        });
                    }
                }
                return images;
            },

        },
        methods: {
            close(){
                this.modal = false;
                this.viewerShow = false;
                document.getElementsByTagName('body')[0].classList.remove("nonscroll");
            },
            showVideo(item){
                this.currentId = item.id;
                this.modal = true;
                document.getElementsByTagName('body')[0].classList.add("nonscroll");
            },
            inited (viewer){
                this.$viewer = viewer;
            },
            showViewer(item){
              document.getElementById(this.uid).querySelector('.my-gallery').querySelectorAll('figure')[item].click();
              this.currentSlideViewer = item;
            },
            updateSlide(payload){
              this.currentSlideViewer = payload.currentSlide;
            },
            updateCarousel(){
                if(this.$refs.carousel){
                    setTimeout(()=>{
                        if(this.$refs.carousel !== undefined){
                            this.$refs.carousel.updateWidth();
                        }
                    },3);
                }
            }
        }
    }
</script>

