<template>
  <div class="content">
    <catalog :favorites="favorites"></catalog>
  </div>
</template>

<script>
    import Catalog from '@/components/catalog/Catalog.vue'
    import {mapState} from 'vuex';

    export default {
        props: {
          favorites: {
          default: function () {
              return false
              }
          }
        },
        metaInfo() {
            return {
                title: this.title,
                titleTemplate: this.$store.state.options.titleTemplate
            }
        },
        name: "Coupons",
        components: {
            Catalog
        },
        methods: {
          getSearch(){
            this.$store.dispatch('actions/initialSearchState');
            this.$store.dispatch('actions/getCouponList', {currentSection: false, isSearch: true,reload: true,});
          },
        },
        computed: {
            title () {
                if(this.$route.params.sid > 0){
                    return this.$store.getters['actions/getTitle'](this.$route.params.sid);
                } else if(this.$route.params.id > 0) {
                    return this.action ? this.action.name : '';
                } else {
                    return 'СКИДКИ АЛЕКСЕЯ ЗЕМСКОВА — ДЛЯ ВСЕХ!';
                }
            },
            ...mapState({
                action: state => state.actions.detail,
            }),
            mounted: function () {
                ym(39511350, 'hit', this.$route.path, {title: this.title+' | ZemSkidki.ru'});
            }
        },
    }
</script>
