                    list = this.action.priceList.value;
                }

                return list;
            },
            token() {
                return this.localStorage.token !== 'false' && this.localStorage.token !== null && this.localStorage.token !== undefined ? this.localStorage.token : false
            },
            countHowToUse() {
                let i = 0;
                if (this.action.callUsJson) {
                    i++;
                }
                if (this.action.comeUsJson) {
                    i++;
                }
                if (this.action.goLinkJson) {
                    i++;
                }
                return i;
            },
            ...mapState({
                popups: state => state.popups.items,
                formAuthFields: state => state.auth.forms.auth.fields,
                action: state => state.actions.detail,
                similars: state => state.actions.detail.similars,
                user: state => state.users.user,
                isBanned: state => state.auth.isBanned,
                isLoadDetail: state => state.actions.isLoadDetail,
                currentCity: state => state.cities.current,
                cities: state => state.cities.items,
                visited: state => state.actions.visited,
                previousSection: state => state.actions.previousSection,
            }),
            backSection() {
                let sectionID = this.action.section ? this.action.section.id : '';

                if (this.previousSection && this.action.sectionNames[this.previousSection]) {
                    sectionID = this.previousSection;
                }
                return {id: sectionID, name: this.action.sectionNames[sectionID]};
            },
            cityDiffMain() {
                let city = this.citiesDiff.find(c => c.isCapital === true);
                if (!city && this.citiesDiff.length > 0) {
                    city = this.citiesDiff[0];
                }
                if (!city) {
                    city = {};
                }
                return city;
            },
            citiesDiffImplode() {
                let cities = this.citiesDiff.filter(c => c.xmlId !== this.cityDiffMain.xmlId);
                let citiesImplode = [];
                for (let city of cities) {
                    citiesImplode.push(city.name);
                }
                return citiesImplode.join(', ');
            },
            citiesDiff() {
                let result = [];

                for (let cityIndex in this.action.city) {
                    let city = this.action.city[cityIndex];
                    let findCity = [...this.cities].find(c => c.xmlId === city);
                    if (findCity) {
                        result.push(findCity);
                    }
                }
                return result;
            },
            isCityDiff() {
                return this.action.city.find(c => c === this.currentCity.xmlId || c === 'all');
            },
            opinionError() {
                if (this.user.id && this.action) {
                    for (let opinion of this.user.opinions) {
                        if (+opinion.action === +this.action.id && +opinion.action > 0) {
                            return 'isSend'
                        }
                    }
                    let isOrder = false;
                    for (let order of this.user.orders) {
                        if (+order.action === +this.action.id && +order.action > 0) {
                            isOrder = true;
                        }
                    }
                    if (!isOrder) {
                        return 'notOrder'
                    }
                }
                return false
            },
            originDiscountHtml() {
                return this.action.originDiscount ? actionDiscountHtml(this.action.originDiscount, this.action.discount) : ''
            },
            onenessDiscountHtml() {
                return this.action.onenessDiscount ? actionDiscountHtml(this.action.onenessDiscount, this.action.discount) : ''
            },
            aboutGallery(){
                let items = [];
                items.push({
                    "path":this.action.image,
                    "icon":this.action.image_for_viewer,
                    "mediaType": 'photo',
                    "width": this.action.preview.width,
                    "height": this.action.preview.height
                });
                for(let i in this.action.aboutActionVideo){
                    if(i>2){
                        break;
                    }
                    items.push(this.action.aboutActionVideo[i])
                }
                for(let i in this.action.aboutActionPhoto){
                    items.push(this.action.aboutActionPhoto[i])
                }
                return items
            }
        },
        methods: {
            headerFixed(){
                let header = document.getElementById('detail-header'),
                    scrollPrev = 0,
                    fixedButton = document.getElementById('btn-fixed');
                window.addEventListener('scroll', function() {
                    let scrolled = window.pageYOffset;
                    if(scrolled > 54){
                        header.classList.add('h-fixed');
                        if ( scrolled > 54 && scrolled > scrollPrev ) {
                            header.classList.add('out');
                        } else {
                            header.classList.remove('out');
                        }
                        scrollPrev = scrolled;
                    }else if(scrolled == 0){
                        header.classList.remove('h-fixed');
                    }
                    if(scrolled < 54){
                      header.classList.remove('out');
                      header.classList.remove('h-fixed');
                    }

                    if(scrolled > 570){
                        fixedButton.classList.add('show');
                    }else{
                        fixedButton.classList.remove('show');
                    }
                });
            },
            sectionMenuOpen(){
                this.$store.commit('actions/sectionMenuOpen');
            },
            topMenuOpen(){
                this.$store.commit('menus/topMenuOpen');
            },
            formatUnit(unit) {
                unit.replace('м2', 'м<sup>2</sup>');
                unit.replace('м3', 'м<sup>3</sup>');
                return unit;
            },
            calcDiscount(price) {
                return parseInt(price) - (parseInt(price) / 100) * this.action.discount;
            },
            number_format: function (v) {
                return number_format(v, 0, '', ' ')
            },
            balloonTemplate(address) {
                return `
                  <div class="yamap_baloon_link yamap_baloon_link_address hover">
                    ` + this.action.provider.name + `<br><br>
                    <b class="yamap_baloon_link-name">` + this.action.name + `</b><br/><br>
                    ` + address + `
                  </div>
                `
            },
            openAverageCheck(){
              this.$store.commit('popups/open', {name: this.popups.averageCheck.name});
            },
            openSupplierInfo(){
              this.$store.commit('popups/open', {name: this.popups.supplierInfo.name});
            },
            closeWhatDiscount(){
              this.$store.commit('popups/close', {name: this.popups.whatDiscount.name});
            },
            openWhatDiscount(){
              this.$store.commit('popups/open', {name: this.popups.whatDiscount.name});
            },
            closeHowToGetDiscount(){
              this.$store.commit('popups/close', {name: this.popups.howToGetDiscount.name});
            },
            openHowToGetDiscount(){
              this.$store.commit('popups/open', {name: this.popups.howToGetDiscount.name});
            },
            closeSupplierInfo(){
              this.$store.commit('popups/close', {name: this.popups.supplierInfo.name});
            },
            openMap: function (key) {
                let markers = {},
                    coords;

                if (this.action && this.action.comeUsJson.coord.value) {
                    coords = this.action.comeUsJson.coord.value.split(',');

                    markers[0] = [coords[1], coords[0]];

                    for (let index in this.action.comeUsJson.otherAddresses) {
                        let coords = this.action.comeUsJson.otherAddresses[index].coord.value.split(',');
                        markers[parseInt(index) + 1] = [coords[1], coords[0]];
                    }

                    this.mapCenter = markers[key];
                    this.$store.commit('popups/open', {name: this.popups.address.name});
                }
            },
            getMarkers: function () {
                let markers = [];
                if (this.action.comeUsJson.coord.value) {
                    let coords = this.action.comeUsJson.coord.value.split(',');

                    markers.push({
                        "id": 10,
                        "type": "placemark",
                        "coords": [coords[1], coords[0]],
                        "hint": "",
                        "balloon": {
                            "header": "",
                            "body": this.balloonTemplate(this.action.comeUsJson.address.value),
                            "footer": ""
                        },
                        "cluster": "1"
                    });

                }

                for (let index in this.action.comeUsJson.otherAddresses) {
                    if (this.action.comeUsJson.otherAddresses[index].coord.value) {
                        let coords = this.action.comeUsJson.otherAddresses[index].coord.value.split(',');
                        let obj = {
                            "id": parseInt(index) + 1,
                            "type": "placemark",
                            "coords": [coords[1], coords[0]],
                            "hint": "",
                            "balloon": {
                                "header": "",
                                "body": this.balloonTemplate(this.action.comeUsJson.otherAddresses[index].address.value),
                                "footer": ""
                            },
                            "cluster": "1"
                        };
                        markers.push(obj);
                    }
                }

                return markers;
            },
            openOpinionAdd: function () {
                if (!!this.user.id) {
                    this.$store.commit('popups/open', {name: this.popups.opinionAdd.name});
                } else {
                    this.$store.commit('auth/setIsRedirect', false);
                    this.$store.commit('popups/open', {name: this.popups.publicAuth.name});
                }
            },
            openMailZems: function (typeReport) {
                this.typeReport = typeReport;
                this.$store.commit('popups/close', {name: this.popups.opinionErrorSend.name});
                this.$store.commit('popups/close', {name: this.popups.opinionError.name});
                this.$store.commit('popups/open', {name: this.popups.mailZems.name});
            },
            openAvailableCities: function () {
                this.availableCitiesOpened = true;
            },
            openHowCalculated: function () {
                this.$store.commit('popups/open', {name: this.popups.howCalculated.name});
            },
            openCouponClaim: function () {
                this.$store.commit('popups/open', {name: this.popups.couponClaim.name});
                this.$store.commit('auth/setIsRedirect', false);
            },
            openCouponBuy: function () {
                this.checkToken();
                if(!this.isBanned){
                    this.$store.commit('auth/setIsRedirect', false);
                    this.$store.commit('popups/open', {name: this.popups.couponBuy.name});
                }
                else{
                    this.$store.commit('popups/open', {name: this.popups.manyAuth.name});
                }
            },
            openCouponBanned: function () {
                this.$store.commit('popups/open', {name: this.popups.couponBanned.name});
            },
            openProviderBanned: function () {
                this.$store.commit('popups/open', {name: this.popups.providerBanned.name});
            },
            openCouponArchived: function () {
                this.$store.commit('popups/open', {name: this.popups.couponArchived.name});
            },
            openCouponNotAvailable: function () {
                this.$store.commit('popups/open', {name: this.popups.couponNotAvailable.name});
            },
            openPhoneNumberHint: function () {
                this.$store.commit('popups/open', {name: this.popups.phoneNumberHint.name});
            },
            openPurchaseAmount: function () {
                this.$store.commit('popups/open', {name: this.popups.purchaseAmount.name});
            },
            openUnavailableLink: function(){
              this.$store.commit('popups/open', {name: this.popups.unavailableLink.name});
            },
            closeCouponBuy(){
                this.$store.commit('popups/close', {name: this.popups.couponBuy.name});
            },
            closeCouponAlreadyIssued(){
                this.$store.commit('popups/close', {name: this.popups.couponAlreadyIssued.name});
            },
            closeCouponBuyError(){
                this.$store.commit('popups/close', {name: this.popups.couponBuyError.name});
            },
            closeCouponBuyErrorAuth(){
                this.$store.commit('popups/close', {name: this.popups.couponBuyErrorAuth.name});
            },
            setFavorite: function () {
                let action = {...this.action};
                action.isDetail = true;
                if(!this.action.favorite){
                  this.$store.commit('popups/open', {name: this.popups.addedFav.name});
                }
                this.$store.dispatch('actions/setFavorite', action);
            },
            activeSection: function (id) {
                if (id) {
                    this.$store.commit('actions/setCouponList', {items: []});
                    this.$store.commit('actions/setPage', 0);
                    this.$store.commit('actions/previousSection', id);
                    this.$store.commit('actions/setViewType', 'list');
                    this.$router.push('/sid' + id)
                    this.$store.commit('actions/activeCategory', {currentSection: id});
                    this.$store.dispatch('actions/activeCategory', {
                        currentSection:id,
                        isLoad: true,
                    });
                    let actionId = this.action.id;
                    this.$store.commit('actions/setCouponToScroll', actionId);
                    //setTimeout(function(){scrollTo('#id'+actionId,true,-70);},1200);
                }
            },
            declOfNumber(number, titles) {
                return declOfNumber(number, titles);
            },
            changeTab(data) {
                this.currentTab = data.tab.hash;
                if (this.currentTab === '#tab-reviews' && this.action.id) {
                    this.loadOpinion = true;
                }
            },
            checkToken() {
                if (!checkToken) {
                    this.localStorage.token = 'false';
                    this.$store.dispatch('auth/logout');
                }
            },
            showImage(ref){
                document.querySelector('figure').click();
            },
            handleScroll: function () {
                let scroll = window.scrollY;
                let viewport = window.innerHeight;
                let bodyHeight = document.getElementById('app').offsetHeight;
                if(bodyHeight-80 <= scroll + viewport && this.init){
                    this.$store.dispatch('actions/getSimilars', {
                        id: this.action.id,
                    })
                }
            },
        }

    }

    function checkToken() {
        return localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined
    }
</script>

<style>
    .yamap_baloon_link.yamap_baloon_link_address {
        position: relative;
        background-size: 100px;
        padding-left: 0px;
        min-height: 100px;
        max-width: 224px;
        color: #545454 !important;
        font-size: 12px;
        line-height: 15px;
    }

    .yamap_baloon_link.yamap_baloon_link_address img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100px;
        height: 100px;
        border-radius: 10px;
    }

    .yamap_baloon_link.yamap_baloon_link_address .yamap_baloon_link-name {
        color: #2F80ED;
        font-weight: bold;
    }
    .popup--map .popup-content{
        padding-bottom: 0;
    }
    .popup--map .popup-content .ymap-container{
        height: 100vh !important;
    }
</style>
