<template>
    <div>
        <div class="popup-header">
            <div class="popup-name">Оформление купона</div>
            <div class="close" @click="close()"><span class="i icon-close">
                          <svg class="icon">
                            <use xlink:href="@/assets/img/mob-sprite.svg#close"></use>
                          </svg></span></div>
        </div>

        <div class="popup-content">
            <div class="inner">
                <div class="content-ttl">На кого оформляем?</div>
                <input-styled :value="user.login"
                              name="login"
                              :placeholder="'Телефон'"
                              :isEdit="true"
                              :disabled="true"
                              :is-block-arrow-phone="true"
                              :mask="{mask:'+7(999)999-99-99',showMaskOnHover: false}"
                              :isValidate="false"
                              @check="showMessageLogin"
                              :type="'tel'"
                              @edit="edit"
                ></input-styled>

                <input-styled v-model="firstName"
                              :placeholder="'Как к вам обращаться?'"
                              name="firstName"
                              class="mt40 mb32"
                              :disabled="formRegistrationFields.firstName.disabled"
                              :isValidate="formRegistrationFields.firstName.disabled"
                              :placeholder-input="'Иван'"
                              @enter="enter(formRegistrationFields.firstName.name)"
                              :isError="formRegistrationFields.firstName.error"
                              :errorText="formRegistrationFields.firstName.errorText"
                              @focus="removeError(formRegistrationFields.firstName.name)"
                              @input="removeError(formRegistrationFields.firstName.name)"
                ></input-styled>

                <span class="inp-s-label --permanent" >Комментарий (необязательно)</span>
                <textarea-field v-model="comment"
                                :class="'full'" placeholder="Например, «Жду звонка как можно скорее!» или «Звонить лучше после обеда...»"
                 :add-style="{height: 149 + 'px'}"></textarea-field>
                <div class="button-block">
                    <submit-btn tag="button"
                                @click.native="buy(action.id)"
                                :type="buttonBuy">
                        Оформить купон
                    </submit-btn>
                </div>
                <div class="underbtn"><span>Оформляя купон, вы соглашаетесь<br>с <router-link  target= '_blank' to="/offer/" class="link-dark-w">публичной офертой</router-link>.</span></div>
            </div>
        </div>
    </div>
</template>

<script>
    import InputStyled from '@/components/forms/InputStyled.vue'
    import { mapState } from 'vuex'
    import SubmitBtn from '@/components/forms/SubmitBtn.vue';
    import TextareaField from '@/components/forms/TextareaField.vue';

    export default {
        name: "CouponBuy",
        components: {
            InputStyled,
            SubmitBtn,
            TextareaField,
        },
        props: {
            action: {
                default(){
                    return {}
                },
            }
        },
        data() {
            return {
                messageLogin: false,
                card: 0,
                buttonBuy: 'normal',
                comment: '',
            }
        },
        computed: {
            firstName: {
                get () {
                    return this.formRegistrationFields.firstName.value
                },
                set (value) {
                    this.$store.commit('auth/setFormRegistrationField', {
                        name: this.formRegistrationFields.firstName.name,
                        value:value,
                    })
                }
            },
            /*lastName: {
                get () {
                    return this.formRegistrationFields.lastName.value
                },
                set (value) {
                    this.$store.commit('auth/setFormRegistrationField', {
                        name: this.formRegistrationFields.lastName.name,
                        value:value,
                    })
                }
            },*/
            /*secondName: {
                get () {
                    return this.formRegistrationFields.secondName.value
                },
                set (value) {
                    this.$store.commit('auth/setFormRegistrationField', {
                        name: this.formRegistrationFields.secondName.name,
                        value:value,
                    })
                }
            },*/
            /*email: {
                get () {
                    return this.formRegistrationFields.email.value
                },
                set (value) {
                    this.$store.commit('auth/setFormRegistrationField', {
                        name: this.formRegistrationFields.email.name,
                        value:value,
                    })
                }
            },*/
            cards: {
                get () {
                    let user = this.$store.getters['users/getUser'],
                        cards = [];

                    cards.push({
                        valueIcon: 'i-card3',
                        value: 0,
                        valueText: 'Новая карта'
                    });

                    for(let i = 0;i < user.cards.length; i++) {
                        //default value
                        if (i === user.cards.length - 1 ) {
                            this.$set(this, 'card', user.cards[i].value);
                        }

                        cards.push({
                            valueIcon: user.cards[i].valueIcon,
                            value: user.cards[i].value,
                            valueText: user.cards[i].valueText
                        });
                    }

                    return cards;
                },
                set (value) {
                }
            },
            ...mapState({
                popups: state => state.popups.items,
                user: state => state.users.user,
                formRegistrationFields: state => state.auth.forms.registration.fields,
            }),
        },
        mounted() {
            if(this.user.firstName.trim()){
                this.$store.commit('auth/setFormRegistrationField', {
                    name: this.formRegistrationFields.firstName.name,
                    value:this.user.firstName.trim(),
                });
                this.$store.commit('auth/setFormRegistrationDisabled', {
                    name: this.formRegistrationFields.firstName.name,
                    value:true,
                })
            }
            /*if(this.user.secondName.trim()){
                this.$store.commit('auth/setFormRegistrationField', {
                    name: this.formRegistrationFields.secondName.name,
                    value:this.user.secondName.trim(),
                });
                this.$store.commit('auth/setFormRegistrationDisabled', {
                    name: this.formRegistrationFields.secondName.name,
                    value:true,
                })
            }*/
            /*if(this.user.lastName.trim()){
                this.$store.commit('auth/setFormRegistrationField', {
                    name: this.formRegistrationFields.lastName.name,
                    value:this.user.lastName.trim(),
                });
                this.$store.commit('auth/setFormRegistrationDisabled', {
                    name: this.formRegistrationFields.lastName.name,
                    value:true,
                })
            }*/
            /*if(this.user.email.trim()){
                this.$store.commit('auth/setFormRegistrationField', {
                    name: this.formRegistrationFields.email.name,
                    value:this.user.email.trim(),
                });
                this.$store.commit('auth/setFormRegistrationDisabled', {
                    name: this.formRegistrationFields.email.name,
                    value:true,
                })
            }*/
        },
        methods: {
            close(){
                this.$store.commit('popups/close', {name: this.popups.couponBuy.name});
            },
            showMessageLogin() {
                this.messageLogin = true;
            },
            editField (field) {
                this.$store.commit('auth/setFormRegistrationDisabled', {
                    name: field,
                    value:false,
                })
            },
            enter (field) {
                this.removeError(field);

            },
            edit(){
                this.$store.commit('auth/setStep',{ step:1 })
                this.$store.commit('auth/setFormAuthDisabled',{
                    name: 'login',
                    value: false
                });
                this.$store.dispatch('auth/logout');
            },
            buy (actionId) {
                if(this.checkFields()){
                    this.buttonBuy = 'load';
                    this.$store.dispatch('userCoupons/buy', {'id': actionId, card: this.card, comment: this.comment}).then(()=>{
                        this.buttonBuy = 'ok';
                        //this.$router.push('/personal');
                        this.$store.dispatch('actions/initDetailPage', {route: this.$route});
                        setTimeout(() => {
                            this.buttonBuy = 'normal';
                        }, 300);
                    });

                }
            },
            inputFocus(ref) {
                if(!!this.$refs[ref]){
                    this.$refs[ref].$el.getElementsByClassName('inp-s')[0].focus()
                }
            },
            checkFields () {
                let check = true;
                for(let index in this.formRegistrationFields) {
                    let checkField = true;
                    if(this.formRegistrationFields[index].required === true){
                        if(this.formRegistrationFields[index].value.length < 2) {
                            checkField = false;
                        }
                        if(!checkField) {
                            this.$store.commit('auth/setFormRegistrationError',{
                                name: this.formRegistrationFields[index].name,
                                value: true
                            });
                            check = false;
                        }
                    }
                }
                return check;
            },
            removeError (field) {
                this.$store.commit('auth/setFormRegistrationError',{ name: field, value: false })
            },
        }
    }
</script>

<style>

</style>
