<template>
    <div :class="{'nocomments moderation': opinions.length === 0}">
        <div class="reviews" v-if="opinions.length > 0">
            <div class="review-item"
                 :class="{'opinion--processed':opinion.answer.text !== ''}"
                 v-for="opinion in opinions">
                <svg class="icon-like icon" v-if="opinion.type === 'negative'">
                    <use xlink:href="@/assets/img/mob-sprite.svg#like"></use>
                </svg>
                <svg v-else class="icon-dislike icon">
                    <use xlink:href="@/assets/img/mob-sprite.svg#like"></use>
                </svg>
                <div class="review-info">
                    <div class="top-info">
                            <div class="name">{{ opinion.user.name }}</div>
                            <div class="date-city">({{ getCityName(opinion.city)}}) {{ opinion.dateNum }}</div>
                    </div>
                    <div class="msg" v-html="opinion.text"></div>
                    <div class="review-item reply" v-if="opinion.answer.text !== ''">
                        <svg class="icon-reply icon">
                            <use xlink:href="@/assets/img/mob-sprite.svg#reply"></use>
                        </svg>

                        <div class="review-info">
                            <div class="top-info">
                                <div class="name" v-html="opinion.provider.name"></div>
                            </div>
                            <div class="msg" v-html="opinion.answer.text"></div>
                        </div>
                    </div>
                </div>
            </div>
            <!-- end opinion -->
            <div class="new-review-butt"><a href="javascript:void(0);" @click="openOpinionAdd"><span class="plus">+</span><span>написать отзыв</span></a></div>
        </div>

        <div v-if="isLoad || opinions.length === 0">
            <loader v-if="isLoad"></loader>
            <div v-else>
                <svg class="icon">
                    <use xlink:href="@/assets/img/mob-sprite.svg#nocomment"></use>
                </svg>
                <div class="info">На эту акцию пока<br>нет отзывов. Но это<br>временно...</div>
            </div>
            <div class="new-review-butt"><a href="javascript:void(0);" @click="openOpinionAdd"><span class="plus">+</span><span>написать отзыв</span></a></div>
        </div>

    </div>

</template>

<script>
    import {mapState} from 'vuex';
    import Loader from '@/components/Loader.vue';
    import PopupInfoBlock from '@/components/modal/PopupInfoBlock.vue';

    export default {
        name: "OpinionList",
        components: {
            Loader,
            PopupInfoBlock,
        },
        props: {
            actionId: {
                default() {
                    return 0
                }
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                user: state => state.users.user,
                opinions: state => state.opinions.items,
                isLoad: state => state.opinions.isLoad,
                action: state => state.actions.detail,
            }),
            opinionError() {
                if (this.user.id && this.action) {
                    for (let opinion of this.user.opinions) {
                        if (+opinion.action === +this.action.id && +opinion.action > 0) {
                            return 'isSend'
                        }
                    }
                    let isOrder = false;
                    for (let order of this.user.orders) {
                        if (+order.action === +this.action.id && +order.action > 0) {
                            isOrder = true;
                        }
                    }
                    if (!isOrder) {
                        return 'notOrder'
                    }
                }
                return false
            },
        },
        mounted() {
            this.$store.commit('opinions/setData', {items: []});
            this.$store.commit('opinions/isLoadNextPage', true);
            this.$store.dispatch('opinions/getList', {actionId: this.actionId});
        },
        methods: {
            getCityName(xmlId) {
                let city = this.$store.getters['cities/getByXmlId'](xmlId)
                return city.name;
            },
            openOpinionAdd: function () {
                if (!!this.user.id) {
                  if(this.opinionError == 'notOrder'){
                    this.$store.commit('popups/open', {name: this.popups.errorNotOrder.name});
                  }else if(this.opinionError == 'isSend'){
                    this.$store.commit('popups/open', {name: this.popups.errorIsSend.name});
                  }else{
                    this.$store.commit('popups/open', {name: this.popups.opinionAdd.name});
                  }
                } else {
                    this.$store.commit('auth/setIsRedirect', false);
                    this.$store.commit('popups/open', {name: this.popups.publicAuth.name});
                }
            },
        }
    }
</script>
